/* src/components/FilmGrid.css */
.FilmCntainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 20px;

    justify-content: flex-start;
  }

  .film-grid {
    max-width: 220px;
    padding: 10px;
  }
  
  
  .film-grid img {
    max-width: 220px;
    margin-bottom: 10px;
    cursor: pointer;
  }

  .film-grid span {
    max-width: 220px;
    margin-bottom: 10px;
  }
  
  .film-name {
    cursor: pointer;
    font-weight: bold;
    text-align: center;
  }

  .film-name-noimg {
    background: yellow;
    padding: 40px 0;
  }
  
  .film-popup {
    /* Style your popup as desired */
    /* For example, position it absolutely and add a background color */
    position: absolute;
    background-color: #f9f9f9;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    z-index: 1;
  }
  

  .ImgError
  {
    width: 220px;
  }