/* FilmPopup.css (or any other filename you prefer) */

/* Styling for the entire popup container */
.film-popup {
  position: fixed;
  top: 15%; /* 15% from the top */
  left: 50%;
  transform: translate(-50%, 0); /* Adjusted to center horizontally */
  background-color: rgba(0, 0, 0, 0.4); /* Dark background with 40% transparency */
  border-radius: 8px;
  padding: 0px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  max-width: 80%; /* Adjust as needed */
  height: 70%; /* 100% - 15% top - 15% bottom */
  
  overflow-y: scroll;
  padding-top: 50px;
}

.film-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
}
  
  /* Styling for individual film links */
  .film-popup a {
    display: block;
    margin-bottom: 10px;
    color: #333;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.2s ease;
    padding: 10px;
    border: black;
    border-bottom: 5px;
    text-decoration: underline;
  }

  .film-popup a:first
  {
    margin-top: 50px;
  }

  .film-popup-title {
    margin-top: 500px;
    height: 100px;
    width: 100%;
    background-color: red;
  }

  .close-button {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    font-size: 30px;
    cursor: pointer;
    color: red;

  }
  
  .film-popup a:hover {
    color: #007bff;
  }