/* FilmLinkItem.css */
.film-link-item {
    margin-bottom: 10px;
    border: solid 2px;
    border-color: black;
  }
  
  .film-link-item a {
    text-decoration: underline;
    color: blue;
  }
  
  .film-link-item a:hover {
    text-decoration: underline;
  }
  
  .film-details {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;

  }
  
  .film-details span {
    margin-right: 10px;
  }
  